.curtain {
  position: absolute;
  z-index: 1000;
  height: 100vh;
  width: 100vw;
  background-color: #ffffb9;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: all 2s;
}

.mail1 {
  width: 300px;
}

.mail2 {
  width: 400px;
  height: 300px;
}

.hiding {
  transform: translateY(100vh);
}
